let tooltipTimeout;

// getters
const getPrecinct = (datum) => datum.hasOwnProperty('precinct') ? datum.precinct : null;
const getShootings = (datum) => datum.hasOwnProperty('shootings') ? datum.shootings : null;
const getYear = (datum)=> datum.year;

const years = shootingsByPrecinctYear.map(getYear).reduce((acc, item) => acc.indexOf(item) === -1 ? acc.concat([item]) : acc, []);

const shootingsCounts = shootingsByPrecinctYear.map(getShootings);
const min = (data) => Math.min(...data);
const max = (data) => Math.max(...data);

const shootingsColorScale = scaleLinear({
  domain: [min(shootingsCounts), max(shootingsCounts)],
  range: ['#cea9bd', '#e7466c']
});

const ShootingsMap = ({width, height, progress}) => {
  console.log('ping');
  // const width = 800;
  // const height = 500;
  const centerX = width / 2;
  const centerY = height / 2;
  
  const [yearIndex, setYearIndex] = useState(0);
  const year = useMemo(() => years[yearIndex], [yearIndex])

  const shootingsInCurrentYear = useMemo(
    () => shootingsByPrecinctYear.filter((item) => item.year === year), 
    [year]
  );

  useEffect(() => {
    const newIndex = Math.floor(progress * years.length);
    if(newIndex !== yearIndex){
      setYearIndex(newIndex);
    }
  }, [progress, yearIndex])

  /** 
   * a bit of a hack to scale the map responsively. ideally we could find the boundaries of the
   * map features and scale to that using fitScale.
   * 
   * TODO: Move this down into the child ParentSize component
   *       Remove outer ParentSize component in App.jsx if not needed
   */
  const fitWidth = width;
  const fitHeight = height;
  const initScale = 46000;
  const scale = (width/fitWidth) < (height/fitHeight) ? initScale * (width/fitWidth) : initScale * (height/fitHeight);
  
  const center = [-73.8758790753777, 40.6901959259204];

  const { showTooltip, hideTooltip, tooltipLeft, tooltipTop, tooltipData, tooltipOpen } = useTooltip();

  const onMouseMove = useCallback((e, left, top, data) => {
    if(tooltipTimeout) {
      clearTimeout(tooltipTimeout);
    }
    showTooltip({
      tooltipTop: top,
      tooltipLeft: left,
      tooltipData: data,
    })
  }, [showTooltip]);

  const onMouseLeave = useCallback((e) => {
    tooltipTimeout = window.setTimeout(() => {
      hideTooltip();
    }, 300);
  }, [hideTooltip]) ;

  return (
      <div className="flex">
        <div className="basis-3/4" style={{height: '500px'}}>
          <ParentSize>
            {
              ({width, height}) => {
                return (
                <div className="mx-auto relative" style={{width: width, height: height}}>
                  <svg width={width} height={height}>
                    <Mercator
                      data={precincts.features}
                      translate={[centerX, centerY]}
                      scale={scale}
                      center={center}
                    >
                      {({features}) => 
                        features.map(({feature, path, projection}, i) => {
                          const coords = projection(geoCentroid(feature));
                          const stylesObj = {
                            fill: '#FFF',
                            fontFamily: 'sans-serif',
                            cursor: 'default',
                          };
                          const hasShootingsData = shootingsInCurrentYear.map(getPrecinct).indexOf(feature.properties.Precinct) > -1;
                          const shootingsData = hasShootingsData ? shootingsInCurrentYear.find((item) => getPrecinct(item) === feature.properties.Precinct) : null;
                          const data = {
                            precinct: feature.properties.Precinct,
                            shootingsData: shootingsData,
                          };
                          return (
                            <React.Fragment key={`map-feature-${i}`}>
                              <path 
                                d={path || ''}
                                fill={data.shootingsData != null ? shootingsColorScale(data.shootingsData.shootings) : '#eee'}
                                stroke='#555'
                                strokeWidth={0.5}
                                onMouseLeave={onMouseLeave}
                                onMouseMove={(e) => onMouseMove(e, coords[0], coords[1], data.shootingsData)}
                              />
                              { /** TODO: REMOVE **/ false && <text
                                transform={`translate(${coords})`}
                                fontSize={Math.max(width / 75, 9)}
                                style={stylesObj}
                                textAnchor="middle"
                              >
                                {feature.properties.Precinct}
                              </text>}
                            </React.Fragment>
                          )
                        })
                    }
                    </Mercator>
                  </svg>
                {tooltipOpen && tooltipData && (
                  <Tooltip top={tooltipTop} left={tooltipLeft} style={defaultStyles}>
                    <div>
                      <small>{tooltipData.precinctName}<br />{tooltipData.shootings} shootings</small>
                    </div>
                  </Tooltip>
                )}
                </div>
              )}
            }
          </ParentSize>
        </div>
        <div className="basis-1/4">
          {
            years.map((item, i) => (
              <div className={`border-black border mb-4 p-2 text-center hover:cursor-pointer ${item === year ? 'bg-black text-white' : ''}`} onClick={() => setYearIndex(i)}>
                {item}
              </div>
            ))
          }
        </div>
      </div>
  );
}

export default ShootingsMap;